export default class Proficiency {
    constructor(name, value) {
        this.name = name;
        this.value = value;
    }
    toString() {
        return this.name;
    }
    bonus(level) {
        return this === Proficiency.U ? 0 : this.value + level;
    }
}
Proficiency.U = new Proficiency('U', 0);
Proficiency.T = new Proficiency('T', 2);
Proficiency.E = new Proficiency('E', 4);
Proficiency.M = new Proficiency('M', 6);
Proficiency.L = new Proficiency('L', 8);
