export default class HitPoints {
    constructor(max, current, temporary = null, dying = null, wounded = null, resistances = [], conditions = []) {
        this.temporary = null;
        this.dying = null;
        this.wounded = null;
        this.resistances = [];
        this.conditions = [];
        this.max = max;
        this.current = current;
        this.temporary = temporary;
        this.dying = dying;
        this.wounded = wounded;
        this.resistances = Array.isArray(resistances) ? resistances : [resistances];
        this.conditions = Array.isArray(conditions) ? conditions : [conditions];
    }
}
