export default class Strike {
    constructor(weapon, keyAbility, proficiency, damageDice, damageBonus, damageType, other = [], traits = [], special = null, wSpec = null, otherBonus = null) {
        this.weapon = weapon;
        this.keyAbility = keyAbility;
        this.proficiency = proficiency;
        this.damageDice = damageDice;
        this.damageBonus = damageBonus;
        this.damageType = damageType;
        this.otherNotes = Array.isArray(other) ? other : [other];
        this.traits = Array.isArray(traits) ? traits : [traits];
        this.special = special;
        this.wSpec = wSpec;
        this.otherBonus = otherBonus;
    }
}
